import {useCollectionData} from 'react-firebase-hooks/firestore'


import './App.css'
import logo from './logo.svg';

const firebaseConfig = {
  apiKey: "AIzaSyCnyS_yVsC7LFfiCIP8U64jclOGnwYUhcI",
  authDomain: "sitesnoticias-webscrapper.firebaseapp.com",
  projectId: "sitesnoticias-webscrapper",
  storageBucket: "sitesnoticias-webscrapper.appspot.com",
  messagingSenderId: "248716292469",
  appId: "1:248716292469:web:863d10b10a3e8610a5a33c",
  measurementId: "G-S4EBXSZHQM"
};
firebase.initializeApp(firebaseConfig)
const firestore = firebase.firestore()

function App() {
  const NoticiasRef = firestore.collection('NoticiasAtuaais')
  const query = NoticiasRef.orderBy('DataPesquisa').limit(1)

  const [dados] = useCollectionData(query, {idField: 'id'})

  
  function ItemNoticia(props) {
    const Noticia = props.Noticia
    return (<>
    
      <section className='itemNoticia'>
        <picture>
          <img src={Noticia.URLimagem} />
        </picture>
        <div>
            <p className='titulo'>{Noticia.titulo}</p>
            <p className='subtitulo'>{Noticia.subtitulo}</p>
            <p className='fonte'>Fonte : {Noticia.site}</p>
            <a href={Noticia.linkMateria} target='_black'><button>Ler noticia</button></a>
          </div>
      </section>
  
    </>)
  }


  return (<>
  <main>
     { dados && dados[0].dadosNoticias.map( item => <ItemNoticia Noticia={item} /> ) }
    </main>
    </>);
}

export default App;
